<template>
    <div id="app">
        <div class="window_wrap">
            <div class="open" v-show="isOnline||currQuesIndex>-1||currQuesIndex>-1">
                <div class="mask" v-if="currStuRec">
                    <el-dialog title="提示" :center="true" :visible.sync="showReconnect" width="50%"
                        :append-to-body="false" :modal-append-to-body="false">
                        <span>批改服务连接失败，是否重新连接?</span>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="showReconnect = false">否</el-button>
                            <el-button type="primary" @click="WSReConnect">是</el-button>
                        </span>
                    </el-dialog>
                    <div class="currStuRec">
                        <div class="recStuInfo">
                            <div class="modelTitle" v-if="openType===-1">
                                <div class="type">未批改</div>
                            </div>
                            <div class="modelTitle" v-else>
                                <div class="type wrong" v-if="openType===0">错误</div>
                                <div class="type haha" v-if="openType===5">半对</div>
                                <div class="type right" v-if="openType===10">正确</div>
                                <button class="btn_repi" @click="repi" :disabled="reping"
                                    :class="{disabled: reping}">重批</button>
                            </div>
                            <div class="result" v-if="!currQuesInfo.isSubject">
                                识别结果：<span>{{currStuRec.recogContent}}</span></div>
                        </div>
                        <div class="judgeContent" ref="judgeContent">
                            <div class="recImg">
                                <canvas id="judge" :width="CW" :height="CH" v-show="openType===-1||reping"></canvas>
                                <vue-scroll :ops="ops">
                                    <img :src="currStuRec.recogImgUrl +'/resize,w_'+imgWidth" alt="">
                                </vue-scroll>
                                <div class="judgeTip" v-show="WStatus==='connecting'">正在初始化批改框...</div>
                                <div class="judgeTip" v-show="openType===-1||reping">请在绿框区域内打 ✓ 或 × 进行批改。</div>
                            </div>

                            <div class="modelStuList" v-if="currQuesInfo">
                                <vue-scroll>
                                    <div class="userItem" v-if="openType===-1 && currQuesInfo.unhandle"
                                        v-for="stu in currQuesInfo.unhandle" :key="stu.deviceMac"
                                        @click="changeStu(stu)" style="cursor:pointer">
                                        <div class="avatar" :class="{border: currStuRec.deviceMac===stu.deviceMac}">
                                            <img :src="stu.avatar" />
                                            <img class="right"
                                                :class="{animationed: animationedStuRight===stu.deviceMac}"
                                                src="@/assets/c_right.png" />
                                            <img class="wrong"
                                                :class="{animationed: animationedStuWrong===stu.deviceMac}"
                                                src="@/assets/c_wrong.png" />
                                            <img class="half" :class="{animationed: animationedStuHalf===stu.deviceMac}"
                                                src="@/assets/c_half.png" />
                                        </div>
                                        <div class="name">{{stu.deviceAlias}}</div>
                                        <img src="@/assets/arrow_green.png" class="arrow"
                                            v-if="currStuRec.deviceMac===stu.deviceMac" />
                                    </div>
                                    <div class="userItem" v-if="openType===10" v-for="stu in currQuesInfo.correct"
                                        :key="stu.deviceMac" @click="changeStu(stu)" style="cursor:pointer">
                                        <div class="avatar" :class="{border: currStuRec.deviceMac===stu.deviceMac}">
                                            <img :src="stu.avatar" />
                                            <img class="right"
                                                :class="{animationed: animationedStuRight===stu.deviceMac}"
                                                src="@/assets/c_right.png" />
                                            <img class="wrong"
                                                :class="{animationed: animationedStuWrong===stu.deviceMac}"
                                                src="@/assets/c_wrong.png" />
                                            <img class="half" :class="{animationed: animationedStuHalf===stu.deviceMac}"
                                                src="@/assets/c_half.png" />
                                        </div>
                                        <div class="name">{{stu.deviceAlias}}</div>
                                        <img src="@/assets/arrow_green.png" class="arrow"
                                            v-if="currStuRec.deviceMac===stu.deviceMac" />
                                    </div>
                                    <div class="userItem" v-if="openType===0" v-for="stu in currQuesInfo.mistake"
                                        :key="stu.deviceMac" @click="changeStu(stu)" style="cursor:pointer">
                                        <div class="avatar" :class="{border: currStuRec.deviceMac===stu.deviceMac}">
                                            <img :src="stu.avatar" />
                                            <img class="right"
                                                :class="{animationed: animationedStuRight===stu.deviceMac}"
                                                src="@/assets/c_right.png" />
                                            <img class="wrong"
                                                :class="{animationed: animationedStuWrong===stu.deviceMac}"
                                                src="@/assets/c_wrong.png" />
                                            <img class="half" :class="{animationed: animationedStuHalf===stu.deviceMac}"
                                                src="@/assets/c_half.png" />
                                        </div>
                                        <div class="name">{{stu.deviceAlias}}</div>
                                        <img src="@/assets/arrow_green.png" class="arrow"
                                            v-if="currStuRec.deviceMac===stu.deviceMac" />
                                    </div>
                                    <div class="userItem" v-if="openType===5" v-for="stu in currQuesInfo.halfAndHalf"
                                        :key="stu.deviceMac" @click="changeStu(stu)" style="cursor:pointer">
                                        <div class="avatar" :class="{border: currStuRec.deviceMac===stu.deviceMac}">
                                            <img :src="stu.avatar" />
                                            <img class="right"
                                                :class="{animationed: animationedStuRight===stu.deviceMac}"
                                                src="@/assets/c_right.png" />
                                            <img class="wrong"
                                                :class="{animationed: animationedStuWrong===stu.deviceMac}"
                                                src="@/assets/c_wrong.png" />
                                            <img class="half" :class="{animationed: animationedStuHalf===stu.deviceMac}"
                                                src="@/assets/c_half.png" />
                                        </div>
                                        <div class="name">{{stu.deviceAlias}}</div>
                                        <img src="@/assets/arrow_green.png" class="arrow"
                                            v-if="currStuRec.deviceMac===stu.deviceMac" />
                                    </div>
                                </vue-scroll>
                            </div>
                        </div>
                        <!-- <div class="avatar"><img :src="currStuRec.avatar" alt=""><p>{{currStuRec.deviceAlias}}</p></div> -->
                        <div class="modelFooter">
                            <div class="btn-close" @click="closeRec">关闭</div>
                        </div>

                    </div>
                </div>

                <vue-scroll :ops="ops">
                    <div class="stuInfo" v-show="currMode" v-if="currStuMac" style="box-sizing: border-box;">
                        <template v-for="(item,index) in currStuInfo">
                            <img v-if="getVersion()"
                                :src="item.recogImgUrl +'?x-oss-process=image/resize,w_'+imgWidth + '&v='+getVersion()"
                                alt="" @dblclick="checkTrails(index)">
                            <img v-else :src="item.recogImgUrl +'?x-oss-process=image/resize,w_'+imgWidth" alt=""
                                @dblclick="checkTrails(index)">
                        </template>

                    </div>
                    <div class="quesInfo" v-loading="loadingQues" v-show="!currMode"
                        v-if="currQuesIndex>-1 && currQuesInfo">
                        <div class="onlineInfo" v-if="isOnline">
                            <div class="question" v-if="currQuesInfo">
                                <div class="quesBody" v-html="currQuesInfo.quesBody"></div>
                                <div class="quesAnalysis" v-html="currQuesInfo.quesAnalysis"></div>
                                <div class="quesAnswer">
                                    正确答案：
                                    <div v-if="!currQuesInfo.isSubject" style="text-align: right;display: inline;">
                                        {{currQuesInfo.quesAnswer}}</div>
                                    <div v-else v-html="currQuesInfo.quesAnswer"></div>
                                </div>
                            </div>
                            <div class="title" v-show="currQuesInfo.unhandle.length">
                                未批改：{{currQuesInfo.unhandle.length}}人</div>
                            <div class="userList" v-show="currQuesInfo.unhandle.length">
                                <div class="userItem" v-if="currQuesInfo.unhandle" v-for="stu in currQuesInfo.unhandle"
                                    :key="stu.deviceMac" @click="checkRec(stu,-1)" style="cursor:pointer">
                                    <div class="avatar"><img :src="stu.avatar" /></div>
                                    <div class="name">{{stu.deviceAlias}}</div>
                                </div>
                            </div>
                            <div class="title">回答正确：{{currQuesInfo.correct.length}}人</div>
                            <div class="userList">
                                <div class="userItem" v-if="currQuesInfo.correct" v-for="stu in currQuesInfo.correct"
                                    :key="stu.deviceMac" @click="checkRec(stu,10)" style="cursor:pointer">
                                    <div class="avatar"><img :src="stu.avatar" /></div>
                                    <div class="name">{{stu.deviceAlias}}</div>
                                </div>
                            </div>
                            <div class="title">回答错误：{{currQuesInfo.mistake.length}}人</div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in currQuesInfo.mistake" :key="stu.deviceMac"
                                    @click="checkRec(stu,0)" style="cursor:pointer">
                                    <div class="avatar"><img :src="stu.avatar" /></div>
                                    <div class="name">{{stu.deviceAlias}}</div>
                                </div>
                            </div>
                            <!-- <div class="title" v-show="currQuesInfo.halfAndHalf.length">
                                半对半错：{{currQuesInfo.halfAndHalf.length}}人</div>
                            <div class="userList" v-show="currQuesInfo.halfAndHalf.length">
                                <div class="userItem" v-for="stu in currQuesInfo.halfAndHalf" :key="stu.deviceMac"
                                    @click="checkRec(stu,5)" style="cursor:pointer">
                                    <div class="avatar"><img :src="stu.avatar" /></div>
                                    <div class="name">{{stu.deviceAlias}}</div>
                                </div>
                            </div> -->
                        </div>
                        <div class="localInfo" v-else>
                            <div class="title">已提交：{{currQuesInfo.submit.length}}人</div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in currQuesInfo.submit" :key="stu.deviceMac">
                                    <div class="avatar"><img :src="stu.avatar" /></div>
                                    <div class="name">{{stu.deviceAlias}}</div>
                                </div>
                            </div>
                            <div class="question">
                                <div class="quesBody" v-html="currQuesInfo.quesBody"></div>
                                <div class="quesAnalysis" v-html="currQuesInfo.quesAnalysis"></div>
                                <div class="quesAnswer">
                                    正确答案：
                                    <div v-if="!currQuesInfo.isSubject" style="text-align: right;">
                                        {{currQuesInfo.quesAnswer}}</div>
                                    <div v-else v-html="currQuesInfo.quesAnswer"></div>
                                </div>
                            </div>
                            <div class="title">未提交：{{currQuesInfo.notsubmitted.length}}人</div>
                            <div class="userList">
                                <div class="userItem" v-for="stu in currQuesInfo.notsubmitted" :key="stu.deviceMac">
                                    <div class="avatar"><img :src="stu.avatar" /></div>
                                    <div class="name">{{stu.deviceAlias}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </vue-scroll>
            </div>
            <div class="window_con">
                <div class="page-header">
                    {{currMode?"学生答题进度":"题目完成进度"}}
                    <div class="intr" v-show="!currMode">
                        <div class="blue">完成</div>
                        <div class="green"  v-if="isOnline">正确</div>
                    </div>
                </div>
                <div class="stuMode" v-show="currMode">
                    <vue-scroll>
                        <div class="list">
                            <ul>
                                <li :class="currStuMac===item.deviceMac?'active':''" class="stuItem"
                                    v-for="(item,index) in stuInfo" :key="item.deviceMac"
                                    @click="setCurrStu(item.deviceMac)">
                                    <div class="avatar"><img :src="item.avatar" alt="">
                                        <p>{{item.deviceAlias}}</p>
                                    </div>
                                    <div class="progress p_student">
                                        <div class="bg">
                                            <div class="loaded"
                                                :style="{width:item.quesNum==0?0:parseInt(item.handleNum*100/item.quesNum)+'%'}">
                                            </div>
                                        </div>
                                    </div>
                                    <div class="percent">
                                        {{item.quesNum==0?0:parseInt(item.handleNum*100/item.quesNum)}}%</div>
                                </li>
                            </ul>
                        </div>
                    </vue-scroll>
                </div>
                <div class="quesMode" v-show="!currMode">
                    <vue-scroll>
                        <div class="list">
                            <ul>
                                <li :class="currQuesIndex===index?'active':''" class="stuItem"
                                    v-for="(item,index) in answerInfo" :key="item.deviceMac"
                                    @click="setCurrQues(index,item.qid)">
                                    <div class="avatar">
                                        <div class="index">{{index+1}}</div>
                                    </div>
                                    <div class="progress p_answer">
                                        <div class="bg">
                                            <div class="complete"
                                                :style="{width: item.deviceTotal==0?0:parseInt(item.handledNum*100/item.deviceTotal)+'%'}">
                                            </div>
                                        </div>
                                        <div class="bg" v-if="isOnline">
                                            <div class="correct" :style="{width:item.accuracyRate*100+'%'}"></div>
                                        </div>
                                    </div>
                                    <div class="percent">
                                        <div class="double">
                                            {{item.deviceTotal?parseInt(item.handledNum*100/item.deviceTotal):"0"}}%
                                        </div>
                                        <div class="double" v-if="isOnline">{{parseInt(item.accuracyRate*100)}}%</div>
                                    </div>
                                </li>
                            </ul>
                            <div class="quesTip" v-if="!answerInfo">没有获取到试题信息</div>
                        </div>
                    </vue-scroll>
                </div>
                <div class="editBar">
                    <div class="btn-group">
                        <div class="btn-left" :class="{active: currMode}" @click="changeMode1">按学生查看</div>
                        <div class="btn-right" :class="{active: !currMode}" @click="changeMode0">按题目查看</div>
                    </div>
                    <div class="btn-close" @click="closeWindow">关闭</div>
                </div>

            </div>
        </div>

    </div>
</template>
<script>
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    var canvas = null
    var ctx = null;
    var pingInterval = null;
    var arrX = [];
    var arrY = [];
    var arrT = [];
    var offsetX, offsetY;

    export default {
        name: 'App',
        data() {
            return {
                ops: {
                    scrollPanel: {
                        scrollingX: false,
                        scrollingY: true
                    }
                },
                WS: null,
                CH: 0,
                CW: 0,
                TT: 0,
                TL: 0,
                currHandle: [],
                isOnline: true,
                isFirstStu: true,
                isFirstQues: true,
                loadingQues: false,
                loadingBase: true,
                mousedown: false,
                showReconnect: false,
                WStatus: "disconnected",
                taskId: "",
                schoolId: "",
                animationedStuRight: "",
                animationedStuWrong: "",
                animationedStuHalf: "",
                currMode: 1,
                openType: -1,
                reping: false,
                SLock: true,   //批改结果时设锁   完成后解锁。
                currStuMac: "",
                currQuesIndex: -1,
                currStuInfo: null,
                currQuesInfo: null,
                stuInfo: null,
                answerInfo: null,
                currStuRec: null

            }
        },
        computed: {
            imgWidth: function () {
                return parseInt(window.innerWidth * 0.65 - 60)
            },
        },
        mounted() {
            //注册需要外部调用的方法
            window.setProgress = this.setProgress
            window.setBaseData = this.setBaseData
            window.setCurrStu = this.setCurrStu
            window.setStuInfo = this.setStuInfo
            window.setQuesInfo = this.setQuesInfo
            //获取url参数
            this.isOnline = utils.getUrlPrams("isOnline") === "1" ? true : false
            this.taskId = utils.getUrlPrams("taskId")
            this.schoolId = utils.getUrlPrams("schoolId")
            this.selectedMac = utils.getUrlPrams("selectedMac")


            this.currStuRec = ""
            console.log(this.isOnline, this.taskId)
            this.loadStuInfo()
            this.loadQuesInfo()
        },
        methods: {
            getVersion() {
                let deviceMac = this.currStuMac
                let version = 0
                for (let i = 0; i < this.stuInfo.length; i++) {
                    if (deviceMac === this.stuInfo[i].deviceMac) {
                        if (this.stuInfo[i].version) {
                            version = this.stuInfo[i].version
                        }
                    }
                }
                return version
            },
            repi() {
                this.reping = true;
                this.initCanvas()
            },
            WSReConnect() {
                this.initCanvas()
            },
            initCanvas() {
                console.log(this.$refs.judgeContent)
                this.TT = this.$refs.judgeContent.offsetTop
                this.TL = this.$refs.judgeContent.offsetLeft
                this.CW = this.$refs.judgeContent.offsetWidth
                this.CH = this.$refs.judgeContent.offsetHeight - 110
                // console.log(this.CW, this.CH)
                // console.log(this.TT)
                canvas = document.getElementById("judge");
                ctx = canvas.getContext('2d');
                offsetX = canvas.offsetLeft;

                offsetY = canvas.offsetTop;
                this.mousedown = false;
                // ctx.translate(0.5, 0.5);
                ctx.fillStyle = '#000';
                // ctx.globalCompositeOperation = 'destination-over';
                console.log(this.WStatus)
                if (this.WStatus == "disconnected") {
                    this.initWebSocket()
                } else {
                    canvas.addEventListener('touchstart', this.eventDown);
                    canvas.addEventListener('touchend', this.eventUp);
                    canvas.addEventListener('touchmove', this.eventMove);
                    canvas.addEventListener('mousedown', this.eventDown);
                    canvas.addEventListener('mouseup', this.eventUp);
                    canvas.addEventListener('mousemove', this.eventMove);
                }

            },
            //初始化weosocket
            initWebSocket() {
                console.log(this.WStatus)
                // if (this.WStatus === "connected") {
                //     this.WS.close()
                // }
                this.WS = new WebSocket("wss://hwr.ppwrite.com/api/v4.0/iink/document");
                this.WS.onmessage = this.WSOnmessage;
                this.WS.onopen = this.WSOnopen;
                this.WS.onerror = this.WSOnerror;
                this.WS.onclose = this.WSClose;
                this.WStatus = "connecting"
            },
            WSOnmessage(event) {
                // console.log(event.data)
                let res = JSON.parse(event.data)
                //websocket连接成功
                switch (res.type) {
                    case "ack":
                        //设置已连接状态并设置config新建Part
                        console.log("++++++websocket已连接++++++++")
                        this.WStatus = "connected"
                        canvas.addEventListener('touchstart', this.eventDown);
                        canvas.addEventListener('touchend', this.eventUp);
                        canvas.addEventListener('touchmove', this.eventMove);
                        canvas.addEventListener('mousedown', this.eventDown);
                        canvas.addEventListener('mouseup', this.eventUp);
                        canvas.addEventListener('mousemove', this.eventMove);
                        let config = {
                            type: 'configuration',
                            lang: 'en_US',
                            text: {
                                configuration: {
                                    customResources: ['judge'],
                                    addLKText: true
                                }
                            }
                        }
                        this.send(config)
                        this.NCP()
                        this.PING()
                        break;
                    case "exported":
                        if (res['exports']['text/plain']) {
                            console.log(this.SLock)
                            if (this.SLock) {
                                this.currHandle.push(res['exports']['text/plain'])
                                console.log(this.currHandle)
                                if (this.currHandle.length === 1) {
                                    setTimeout(() => {
                                        this.SLock = false
                                        console.log("########################提交")
                                        this.submitHandle()
                                    }, 1500);
                                }
                            }
                        }

                        break;
                    case "error":
                        console.error(res.message)
                    default:
                        break;
                }
            },
            resetRecData() {
                //批改成功后清空画布和存储的笔记数据
                arrX = []
                arrY = []
                arrT = []
                this.animationedStuRight = ""
                this.animationedStuHalf = ""
                this.animationedStuWrong = ""
                this.SLock = true
                this.reping = false
                ctx.clearRect(0, 0, this.CW, this.CH)
                this.currHandle = []
            },
            WSOnopen(event) {
                let args = {
                    type: "newContentPackage",
                    applicationKey: "9870c99c-ac09-4d4c-97df-12c47cf9141f",
                    xDpi: 90,
                    yDpi: 90,
                    viewSizeHeight: 800,
                    viewSizeWidth: 1023
                }
                this.send(args)
            },
            WSOnerror(event) {
                console.log(event)
            },
            NCP() {
                let ncp = {
                    "type": "newContentPart",
                    "contentType": "TEXT",
                    "mimeTypes": ["text/plain"]
                }
                this.send(ncp)
                if (this.currHandle.length > 0) {
                    this.resetRecData()
                }

            },
            PING() {
                let that = this
                let ping = {
                    "type": "ping"
                }
                pingInterval = setInterval(() => {
                    if (that.WStatus === "connected")
                        this.send(ping)
                }, 30000);

            },
            WSClose(event) {
                console.log("--------------close---------------")
                this.currStuRec = null
                this.WS = null
                this.WStatus = "disconnected"
                this.reping = false
                clearInterval(pingInterval);
            },
            submitHandle() {
                let that = this
                let result = 0;
                if (this.currHandle.length === 1) {
                    if (this.currHandle[0].charAt(0) == "✓") {
                        result = 10
                    } else {
                        result = 0
                    }
                } else {
                    if (this.currHandle[1].charAt(0) == "✓") {
                        result = 10
                    } else {
                        result = 0
                    }
                }
                // alert(result)
                let taskId = this.taskId
                let qId = this.currQuesInfo.qId
                let fValue = Math.ceil(this.currQuesInfo.score * result * 0.1)
                let quesTypeId = this.currQuesInfo.quesTid
                let qCorrectType = this.currQuesInfo.recogType
                let deviceMac = this.currStuRec.deviceMac
                axios.post(API.api_qb + 'recogcorrect/setQuesCorrectResult', {
                    taskId: taskId,
                    deviceMac: deviceMac,
                    qId: qId,
                    quesTypeId: quesTypeId,
                    fValue: fValue,
                    qCorrectType: qCorrectType,
                    markPoint: result
                }).then(function (res) {
                    //批改成功
                    // console.log(res.data.data.rate)
                    // console.log(that.answerInfo)
                    for (let i = 0; i < that.answerInfo.length; i++) {
                        if (qId === that.answerInfo[i].qid) {
                            that.answerInfo[i].accuracyRate = res.data.data.rate
                        }

                    }
                    //同步当前学生的试卷图版本号
                    for (let i = 0; i < that.stuInfo.length; i++) {
                        if (deviceMac === that.stuInfo[i].deviceMac) {
                            if (that.stuInfo[i].version) {
                                that.stuInfo[i].version = that.stuInfo[i].version + 1
                            } else {
                                that.stuInfo[i].version = 1
                            }

                        }

                    }

                    // console.log(that.stuInfo)
                    // console.log(that.currStuInfo)

                    if (res.data.code === 0) {
                        console.log("批改成功")
                        console.log(that.CW, that.CH)



                        //判断当前批改结果并将该学生移入新的结果分类中
                        if (result === 10) {
                            that.animationedStuRight = that.currStuRec.deviceMac

                            setTimeout(() => {
                                console.log(that.openType)
                                if (that.openType == -1) {
                                    for (let i = 0; i < that.currQuesInfo.unhandle.length; i++) {
                                        if (that.currQuesInfo.unhandle[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.correct.push(that.currQuesInfo.unhandle[i])
                                            that.currQuesInfo.unhandle.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.unhandle[0]
                                        }
                                    }
                                } else if (that.openType == 5) {
                                    for (let i = 0; i < that.currQuesInfo.halfAndHalf.length; i++) {
                                        if (that.currQuesInfo.halfAndHalf[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.correct.push(that.currQuesInfo.halfAndHalf[i])
                                            that.currQuesInfo.halfAndHalf.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.halfAndHalf[0]
                                        }
                                    }
                                } else if (that.openType == 0) {
                                    for (let i = 0; i < that.currQuesInfo.mistake.length; i++) {
                                        // console.log("+++++++++++++++++")
                                        // console.log(that.currStuRec.deviceMac)
                                        if (that.currQuesInfo.mistake[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.correct.push(that.currQuesInfo.mistake[i])
                                            that.currQuesInfo.mistake.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.mistake[0]
                                        }
                                    }
                                }
                                that.NCP()
                            }, 1000);
                        } else if (result === 5) {
                            that.animationedStuHalf = that.currStuRec.deviceMac
                            setTimeout(() => {
                                if (that.openType == -1) {
                                    for (let i = 0; i < that.currQuesInfo.unhandle.length; i++) {
                                        if (that.currQuesInfo.unhandle[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.halfAndHalf.push(that.currQuesInfo.unhandle[i])
                                            that.currQuesInfo.unhandle.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.unhandle[0]
                                        }
                                    }
                                } else if (that.openType == 10) {
                                    for (let i = 0; i < that.currQuesInfo.correct.length; i++) {
                                        if (that.currQuesInfo.correct[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.halfAndHalf.push(that.currQuesInfo.correct[i])
                                            that.currQuesInfo.correct.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.correct[0]
                                        }
                                    }
                                } else if (that.openType == 0) {
                                    for (let i = 0; i < that.currQuesInfo.mistake.length; i++) {
                                        if (that.currQuesInfo.mistake[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.halfAndHalf.push(that.currQuesInfo.mistake[i])
                                            that.currQuesInfo.mistake.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.mistake[0]
                                        }
                                    }
                                }
                                that.NCP()
                            }, 1000);
                        } else {
                            that.animationedStuWrong = that.currStuRec.deviceMac
                            setTimeout(() => {
                                if (that.openType == -1) {
                                    for (let i = 0; i < that.currQuesInfo.unhandle.length; i++) {
                                        if (that.currQuesInfo.unhandle[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.mistake.push(that.currQuesInfo.unhandle[i])
                                            that.currQuesInfo.unhandle.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.unhandle[0]
                                        }
                                    }
                                } else if (that.openType == 10) {
                                    for (let i = 0; i < that.currQuesInfo.correct.length; i++) {
                                        if (that.currQuesInfo.correct[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.mistake.push(that.currQuesInfo.correct[i])
                                            that.currQuesInfo.correct.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.correct[0]
                                        }
                                    }
                                } else if (that.openType == 5) {
                                    for (let i = 0; i < that.currQuesInfo.halfAndHalf.length; i++) {
                                        if (that.currQuesInfo.halfAndHalf[i].deviceMac === that.currStuRec.deviceMac) {

                                            that.currQuesInfo.mistake.push(that.currQuesInfo.halfAndHalf[i])
                                            that.currQuesInfo.halfAndHalf.splice(i, 1)
                                            that.currStuRec = that.currQuesInfo.halfAndHalf[0]
                                        }
                                    }
                                }
                                //如果当前批改的是为批改分组，则reset笔记栈
                                that.NCP()

                            }, 1000);
                        }

                    }
                }).catch(function (error) {
                    console.log(error);
                });
            },
            send(args) {
                // console.log(args)
                if (this.WS && this.WS.readyState == WebSocket.OPEN) {
                    this.WS.send(JSON.stringify(args));
                }
            },
            eventDown(event) {
                console.log("down")
                event.preventDefault();
                if (this.SLock) {
                    this.mousedown = true;
                    let t = event.timeStamp
                    if (event.changedTouches) {
                        event = event.changedTouches[event.changedTouches.length - 1];
                    }
                    let x = event.clientX - this.TL;
                    let y = event.clientY - this.TT;
                    arrX.push(x)
                    arrY.push(y)
                    arrT.push(t)
                    ctx.beginPath();
                    ctx.strokeStyle = "red";
                    ctx.moveTo(x, y);
                }
            },
            eventUp(event) {
                console.log("up")
                event.preventDefault();
                if (this.SLock) {
                    this.mousedown = false;
                    ctx.lineCap = "round";
                    ctx.lineWidth = 2;
                    ctx.strokeStyle = "red";
                    ctx.stroke();
                    let pointers = {
                        type: "addStrokes",
                        strokes: [
                            {
                                "id": 'pending-1',
                                "pointerType": 'PEN',
                                "x": arrX,
                                "y": arrY,
                                "t": arrT,
                            }
                        ]
                    }
                    if (this.currHandle.length < 2) {
                        this.send(pointers)
                    }
                }


            },

            eventMove(event) {
                // console.log("move")
                event.preventDefault();
                if (this.mousedown && this.SLock) {
                    // console.log(event)
                    let t = event.timeStamp
                    if (event.changedTouches) {
                        event = event.changedTouches[event.changedTouches.length - 1];
                    }
                    let x = event.clientX - this.TL;
                    let y = event.clientY - this.TT;

                    // console.log(t)
                    arrX.push(x)
                    arrY.push(y)
                    arrT.push(t)
                    ctx.lineTo(x, y);
                    ctx.stroke();

                }
            },
            checkRec(stu, openType) {
                // console.log(stu)
                console.log(openType)
                console.log(this.SLock)
                this.currStuRec = stu
                this.openType = openType
                setTimeout(() => {
                    if (openType === -1) {
                        this.initCanvas()
                    }
                }, 200);
            },
            changeStu(stu) {
                //判断是否正在批改中，非批改中状态才可切换
                console.log("LOCK----------------"+this.SLock)
                if (!arrX) {
                    this.currStuRec = stu
                    this.reping = false
                }

            },
            closeRec() {
                this.WSClose()
            },
            setBaseData() {
                this.loadStuInfo()
                this.loadQuesInfo()
            },
            setStuInfo() {
                this.loadStuInfo()
            },
            setQuesInfo() {
                this.loadQuesInfo()
            },
            checkTrails(index) {
                console.log("in......")
                let name = ""
                for (const stu of this.stuInfo) {
                    if (this.currStuMac === stu.deviceMac) {
                        name = stu.deviceAlias
                    }
                }
                window.cefQuery({
                    request: "checkTrailsSuitang:" + index + "&" + name,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            setCurrStu(deviceMac) {
                let that = this
                let taskId = this.taskId
                let schoolId = this.schoolId
                this.changeMode1()
                if (deviceMac === this.currStuMac && !this.isOnline) {
                    this.currStuMac = ""
                    this.closeModal(1)
                } else {
                    this.currStuMac = deviceMac
                    if (this.isOnline) {
                        axios.get(API.api_qb + 'progress/paperByStudent', {
                            params: {
                                taskId: taskId,
                                deviceMac: deviceMac,
                                schoolId: schoolId
                            }
                        }).then(function (res) {
                            console.log(res.data.data)
                            that.currStuInfo = res.data.data
                            localStorage.setItem('currStuInfo', JSON.stringify(that.currStuInfo));
                        }).catch(function (error) {
                            console.log(error);
                        });
                    } else {
                        this.openStuModal(deviceMac)
                    }

                }
            },
            setCurrQues(index, qid) {
                this.currStuRec = null
                if (index === this.currQuesIndex && !this.isOnline) {
                    this.currQuesIndex = -1
                    this.closeModal(2)
                } else {
                    this.currQuesIndex = index
                    this.loadCurrQuesInfo(qid)
                    if (!this.isOnline) {
                        this.openQuesModal(qid)
                    }
                }

            },
            changeMode0() {
                this.currMode = 0
                if (this.currQuesIndex < 0 && this.currStuMac && !this.isOnline) {
                    this.closeModal(2)
                }
                if (!this.isOnline) {
                    this.currModal(2)
                }
            },
            changeMode1() {
                this.currMode = 1
                if (this.currStuMac && this.currQuesIndex > -1 && !this.isOnline) {
                    this.closeModal(1)
                }
                if (!this.isOnline) {
                    this.currModal(1)
                }
            },
            loadCurrQuesInfo(qid) {
                let that = this
                let taskId = this.taskId
                let qId = qid
                this.loadingQues = true
                if (this.isOnline) {
                    axios.get(API.api_qb + 'progress/questionDetail', {
                        params: {
                            taskId: taskId,
                            qId: qId,
                            version: 2
                        }
                    }).then(function (res) {
                        that.loadingQues = false
                        let ques = res.data.data
                        console.log(ques)
                        that.currQuesInfo = ques
                    }).catch(function (error) {
                        console.log(error);
                    });
                } else {
                    axios.get(API.api_qb + 'progress/questionDetail', {
                        params: {
                            qId: qId
                        }
                    }).then(function (res) {
                        that.loadingQues = false
                        let ques = res.data.data
                        window.cefQuery({
                            request: "loadQuesInfo:" + qId,   // 代表请求方法 example事件名称 data 为传入参数
                            onSuccess: function (response) {
                                console.log("invoke success response = " + response);
                                that.loadingQues = false
                                let users = JSON.parse(response).data
                                ques = Object.assign(users, ques)
                                that.currQuesInfo = ques
                            },
                            onFailure: function (ecode, emsg) {
                                console.log("invoke fail code = " + ecode + " msg:" + emsg);
                            }
                        });

                    }).catch(function (error) {
                        console.log(error);
                    });
                    console.log(that.currQuesInfo)
                }
            },
            loadStuInfo() {
                let that = this
                let id = this.taskId
                if (this.isOnline) {
                    axios.get(API.api_qb + 'progress/studentAnswer', {
                        params: {
                            taskId: id
                        }
                    }).then(function (res) {
                        // console.log(res)
                        that.loadingBase = false
                        let tasks = res.data.data
                        that.stuInfo = tasks
                        if (that.isFirstStu) {
                            that.setCurrStu(tasks[0].deviceMac)
                            that.isFirstStu = false
                        }
                    }).catch(function (error) {
                        console.log(error);
                    });
                } else {
                    window.cefQuery({
                        request: "loadStuInfo:" + id,   // 代表请求方法 example事件名称 data 为传入参数
                        onSuccess: function (response) {
                            console.log("invoke success response = " + response);
                            that.loadingBase = false
                            let tasks = JSON.parse(response).data
                            that.stuInfo = tasks
                            //如果url里有需要默认选中的学生则选中，反之选中第一个学生
                            if (that.selectedMac) {
                                for (let i = 0; i < tasks.length; i++) {
                                    if (tasks[i].deviceMac === that.selectedMac) {
                                        that.setCurrStu(tasks[i].deviceMac)
                                        return
                                    }
                                }
                            }
                        },
                        onFailure: function (ecode, emsg) {
                            console.log("invoke fail code = " + ecode + " msg:" + emsg);
                        }
                    });
                }


            },
            loadQuesInfo() {
                let that = this
                let id = this.taskId
                if (this.isOnline) {
                    axios.get(API.api_qb + 'progress/questions/v2', {
                        params: {
                            taskId: id
                        }
                    }).then(function (res) {
                        let tasks = res.data.data
                        that.answerInfo = tasks
                        if (that.isFirstQues) {
                            that.setCurrQues(0, tasks[0].qid)
                            that.isFirstQues = false
                        }
                        that.checkFinish()
                    }).catch(function (error) {
                        console.log(error);
                    });
                } else {
                    window.cefQuery({
                        request: "loadQuesInfo",   // 代表请求方法 example事件名称 data 为传入参数
                        onSuccess: function (response) {
                            console.log("invoke success response = " + response);
                            that.answerInfo = JSON.parse(response).data
                        },
                        onFailure: function (ecode, emsg) {
                            console.log("invoke fail code = " + ecode + " msg:" + emsg);
                        }
                    });
                }
            },
            checkFinish() {
                let tasks = this.answerInfo
                for (const task of tasks) {
                    if (task.deviceTotal !== task.handledNum) {
                        // console.log("1111")
                        setTimeout(() => {
                            this.loadQuesInfo()
                            this.loadStuInfo()
                        }, 2000);
                        return;
                    }
                }
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowSchedule",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            openStuModal(deviceMac) {
                window.cefQuery({
                    request: "openModal:" + deviceMac + "&1",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            openQuesModal(qId) {
                window.cefQuery({
                    request: "openModal:" + qId + "&2",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            closeModal(type) {
                this.currQuesIndex = -1
                this.currStuMac = ""
                window.cefQuery({
                    request: "closeModalSchedule:" + type,   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            currModal(type) {
                window.cefQuery({
                    request: "currModal:" + type,   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            /*
            deviceMac    mac地址
            handleNum    学生已作答题目个数
            quesNum      该试卷题目总数
            handledNum   该题目已作答人数
            deviceTotal  总人数
            qId          题目id
            */
            setProgress(deviceMac, handleNum, quesNum, handledNum, deviceTotal, qId) {
                let stuInfo = this.stuInfo
                let answerInfo = this.answerInfo
                console.log(deviceMac, handleNum, quesNum, handledNum, qId)
                for (let i = 0; i < stuInfo.length; i++) {
                    if (stuInfo[i].deviceMac === deviceMac) {
                        this.$set(this.stuInfo[i], "handleNum", handleNum)
                        this.$set(this.stuInfo[i], "quesNum", quesNum)
                    }
                }
                for (let j = 0; j < answerInfo.length; j++) {
                    if (answerInfo[j].qid === qId) {
                        this.$set(this.answerInfo[j], "handledNum", handledNum)
                        this.$set(this.answerInfo[j], "deviceTotal", deviceTotal)
                    }
                }
            }
        }
    }
</script>
